<template>
	<account activeTab="integrations">
		<section>
			<div class="field_title pt-1 d-flex align-items-center">
				<BIcon icon="code" class="mr-2" scale="1.4" />
				<div class="title font-weight-500">{{ $t("connections") }}</div>
			</div>
			<div class="mt-4">
				<div class="mb-4">
					<div class="d-flex align-items-center">
						<b-img :src="require(`../../assets/media/zoom.png`)" width="50px"></b-img>
						<div class="ml-3">
							<div class="mb-1" :class="{ 'text-muted': zoomEmail }">{{ $t("zoom") }}</div>
							<div>{{ zoomEmail }}</div>
							<div class="text-muted fs-sm" v-if="!zoomEmail">{{ $t("automaticallyCreateZoomMeetings") }}</div>
						</div>
						<div class="ml-auto">
							<span class="btn_icon" v-b-tooltip.hover :title="$t('disconnect')" v-if="zoomEmail" @click="$store.dispatch('zoomDisconnect')">
								<b-iconstack font-scale="0.8">
									<b-icon stacked icon="triangle" shift-v="5"></b-icon>
									<b-icon stacked icon="dash-lg" shift-v="-6"></b-icon>
								</b-iconstack>
							</span>
							<a class="btn_outline" v-else :href="$store.state.auth.zoomRedirectURL">{{ $t("connect") }}</a>
						</div>
					</div>
				</div>
				<div class="mb-4">
					<div class="d-flex align-items-center">
						<b-img :src="require(`../../assets/media/google_meet.svg`)" width="50px"></b-img>
						<div class="ml-3">
							<div class="mb-1" :class="{ 'text-muted': isGoogleConnected }">{{ $t("googleMeet") }}</div>
							<div class="text-muted fs-sm" v-if="!isGoogleConnected">{{ $t("connectGoogleMeetDesc") }}</div>
							<template v-else>
								<div v-if="googleHasWriteCalendar">{{ $t("googleWriteCalendarConnected") }}</div>
								<div v-else>{{ $t("setGoogleWriteCalendar") }}</div>
							</template>
						</div>
						<div class="ml-auto">
							<button class="btn_outline" v-if="!isGoogleConnected">{{ $t("connect") }}</button>
						</div>
					</div>
				</div>
			</div>
		</section>
	</account>
</template>

<script>
	import Account from "@/components/Account";
	export default {
		components: { Account },

		data() {
			return {
				calendarImages: {
					"microsoft.com": "microsoft_teams.svg",
					"google.com": "google_meet.svg",
					zoom: "zoom.png",
				},
				calendarNames: {
					"microsoft.com": this.$t("microsoftTeams"),
					"google.com": this.$t("googleMeet"),
					zoom: this.$t("zoom"),
				},
			};
		},

		async mounted() {
			await this.$store.dispatch("getZoomRedirect");

			let { state, code, provider } = this.$route.query;

			if (state && code && provider) {
				this.$store.dispatch("zoom", { state, code });
			}
		},

		computed: {
			zoomEmail() {
				let zoom = this.$me.integrations.find((integration) => integration.providerName === "zoom");

				return zoom ? zoom.userEmail : "";
			},

			isGoogleConnected() {
				let google = this.$me.integrations.find((integration) => integration.providerName === "google.com");

				return !!google;
			},

			googleHasWriteCalendar() {
				let google = this.$me.integrations.find((integration) => integration.providerName === "google.com");

				return google ? google.calendars.some((cal) => cal.shouldWrite) : false;
			},

			providers() {
				return this.$me.integrations.map((integration) => {
					return {
						name: this.calendarNames[integration.providerName],
						image: this.calendarImages[integration.providerName],
						email: integration.userEmail,
						userId: integration.userId,
						hasDisconnect: !this.$me.authProviders.some((p) => p.userId === integration.userId),
					};
				});
			},
		},
	};
</script>

<style lang="scss"></style>
